//Dependencias
import React from 'react';
import Vision from '../assets/img/Nosotros/Vision.png';
import Mision from '../assets/img/Nosotros/eficacia.png';
import background3 from '../assets/img/Paralax/background3.jpg';




class Nosotros extends React.Component  {
render(){
  return (
    <div className="container Nosotros">
      <div className="row">
        <div className="col s12 m6">
        <img style={{width:"100%",height:'100%'}} src={Vision}></img>
        </div>
        <div className="col s12 m6">
          <br></br>
          <h1>Misión</h1>
          Otorgar  confianza, comodidad  y   seguridad de  salud integral  a cargo de nuestro staff de profesionales  médicos y tecnólogos contribuyendo  una mejor calidad de vida.
        </div>
      </div>
      <div className="row">
        <div className="col s12 m6">
        <br></br>
          <h1>Visión</h1>
          Ser líder  en centro de resonancia magnética a nivel nacional, demostrando ser competentes con tecnologías de puntas e investigación, velando por el bienestar del paciente, convirtiéndonos en el mejor referente en este servicio.
        </div>
        <div className="col s12 m6">
        <img style={{width:"100%",height:'100%'}} src={Mision}></img>
        </div>
      </div>     
    
    </div>
  );
  }
}


export default Nosotros;
